import React, { useState, useCallback, useEffect } from 'react';
import ReactDOM from 'react-dom';
import styled from 'styled-components'

import './index.css';

const AppWrapper = styled.div`
  text-align: center;
`

const AppHeader = styled.header`
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
`

const Border = styled.div`
    position: absolute;
    width: 0px;
    height: 0px;
    border: 3px solid transparent;
    transition: width 0.2s, top 0.2s;
`

const Option = ({ className, children }) => {
    
    const onMouseOver = (ev) => {
        const rect = ev.target.getBoundingClientRect();
        positionBorder(rect.top,rect.height,rect.width)
    }

    const positionBorder = (top, height, width) => {
        document.querySelector('#border').style.top = `${top - 2}px`;
        document.querySelector('#border').style.height = `${height}px`;
        document.querySelector('#border').style.width = `${width}px`;
        document.querySelector('#border').style.border = `3px solid mediumspringgreen`;
    }

    return (
        <span className={className} onMouseOver={onMouseOver}>
            {children}
        </span>
    )
}

const StyledOption = styled(Option)`
  margin-bottom: 10px;
  padding: 10px;
  cursor: pointer;
  transition: border 0.5s;
  z-index: 2;
`

function App() {
    return (
        <AppWrapper>
            <AppHeader>
                {/* 
                    Will add Logo at some point
                    <Img src={logo} alt="logo" /> 
                */}
                <StyledOption>Story mode</StyledOption>
                <StyledOption>Chapter Select</StyledOption>
                <StyledOption>Credits</StyledOption>

                <Border id="border"/>

            </AppHeader>
        </AppWrapper>
    );
}

ReactDOM.render(<App />, document.getElementById('root'));